body {
  padding: 20px;
}

.page-wrap {
  max-width: auto;
  min-width: 800px; 
  min-height:600px; 
  margin: 0 auto;
  border-radius: 0.5em;
  border-style: solid; 
  border-color: black; 
  padding: 2em;
  position: relative; 
}

.extraction-rules{
  max-width: auto;
  margin: 0;
  border-radius: 0.1em;
  border-style: solid; 
  border-color: black; 
  padding: 0em;  
  border-width: 0.05em; 
  height: 600px; 
  overflow: scroll; 
  /*float: bottom;*/
}

 .rulesText{
  max-width: auto;
  margin: auto;
  border-radius: 0.1em;
  border-style: solid; 
  border-color: black; 
  padding: 1em;  
  border-width: 0.05em; 
  overflow: auto; 
  /*float: bottom;*/
}

