.extractionText{
  display: inline; 
  font-weight: bold; 
  font-size: 1.2em; 
}

#appHeader
{
  border: none; 
  position: relative; 
}

#run-rules{
  margin-bottom: 20px; 
}



.button {
  display: inline-block;
  padding: 3px 8px;
  font-size: 14px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color: #A2A1B2;
  border: none;
  border-radius: 5px;
  -webkit-box-shadow: 0 4px #999;
          box-shadow: 0 4px #999;
}

.button:hover {background-color: #355667}

.button:active {
  background-color: #3e8e41;
  -webkit-box-shadow: 0 5px #666;
          box-shadow: 0 5px #666;
  -webkit-transform: translateY(1px);
      -ms-transform: translateY(1px);
          transform: translateY(1px);
}


.textInput
{
  width: 100%; 
  height: 100%; 
}

#ruleMenu{
  margin-bottom: 1.5em; 
}


#result{
  max-width: auto;
  margin: auto;
  border-radius: 0.1em;
  border-style: solid; 
  border-color: black; 
  padding: 1em;  
  border-width: 0.05em; 
}

.listStyle{
  list-style: none;  
}

.resultwrap{
  padding-right: 2em;
}





